* {
  box-sizing: border-box;

}

:root {
  --color-brand-light: #00acdc;
  --color-primary-dark: #000;
}

body {
  margin: 0;
  width: 1200px;
  font-size: 12px;
  font-weight: 500;
  font-family: Roboto, sans-serif;
  font-style: normal;
}



a {
  text-decoration: none;
  cursor: pointer;
  color: var(--color-primary-dark);
  text-transform: uppercase;
  padding-top: 23px;
}

.is-active {
  color: var(--color-brand-light);
}

a:hover{
  color: var(--color-brand-light);
}

[data-qa="hover"]:hover{
  color: var(--color-brand-light)
}

.is-active::after {
  box-sizing: border-box;
  content: "";
  display: block;
  width: 36.6px;
  height: 4px;
  background-color: var(--color-brand-light);
  border-radius: 8px;
  opacity: 1;
  margin-top: 19px;
}

/* a:active::after {
  opacity: 0;
} */

.header__nav {
  display: flex;
  padding: 0 50px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.05);
}

.logo {
  height: 40px;
  width: 40px;
  margin: 10px 424.5px 10px 0;
  line-height: 1;
  padding-top: 0;
}

.header__nav-list {
  list-style: none;
  display: flex;
  padding: 0;
  margin: 0;
  gap: 20px;
}

.nav__item {
  margin: 0;
  display: flex;
  font-weight: 500;
  text-transform: uppercase;
  font-size: 12px;
  color: var(--color-primary-dark);
}


@media screen and (max-width: 1024px){
  body {
    margin: 0;
    width: 1024px;
  }

  .logo {
    height: 40px;
    width: 40px;
    margin: 10px 248.5px 10px 0;
  }

  [data-qa="hover"]:hover{
    color: var(--color-brand-light)
  }

}

